import { Button } from 'ether-ui';
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useCampaignStore } from '../../services/campaign';
import { CampaignList } from './CampaignList';

const Campaigns: React.FC = () => {
    const navigate = useNavigate();
    const { loadCampaigns, campaigns, isLoading } = useCampaignStore();

    useEffect(() => {
        loadCampaigns();
    }, []);

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <div>
                    <h2 className="text-2xl font-bold text-gray-900">Campaigns</h2>
                    <p className="text-gray-600 mt-1">Manage your marketing campaigns</p>
                </div>
                <Button onClick={() => navigate('/campaigns/create')}>Create Campaign</Button>
            </div>

            <CampaignList campaigns={campaigns} isLoading={isLoading} />
        </div>
    );
};

export default Campaigns;
