import classnames from 'classnames';
import React, { useEffect, useRef, useState } from 'react';
import { CampaignRecipient } from '../../types/campaign';

interface RecipientStatusPopoverProps {
    isOpen: boolean;
    onClose: () => void;
    triggerElement: HTMLElement | null;
    recipients: CampaignRecipient[];
    total: number;
    loading?: boolean;
    onLoadMore?: () => void;
    hasMore?: boolean;
}

const statusColorMap: Record<string, string> = {
    pending: '#f59e0b',
    sending: '#3b82f6',
    submitted: '#8b5cf6',
    enqueued: '#6366f1',
    sent: '#8b5cf6',
    delivered: '#10b981',
    read: '#3b82f6',
    responded: '#ec4899',
    internally_failed: '#ef4444',
    externally_failed: '#ef4444',
    retry_exhausted: '#dc2626',
};

const statusLabelMap: Record<string, string> = {
    pending: 'Pending',
    sending: 'Sending',
    submitted: 'Submitted',
    enqueued: 'Enqueued',
    sent: 'Sent',
    delivered: 'Delivered',
    read: 'Read',
    responded: 'Responded',
    internally_failed: 'Failed',
    externally_failed: 'Failed',
    retry_exhausted: 'Failed',
};

export const RecipientStatusPopover: React.FC<RecipientStatusPopoverProps> = ({
    isOpen,
    onClose,
    triggerElement,
    recipients,
    total,
    loading,
    onLoadMore,
    hasMore,
}) => {
    const popoverRef = useRef<HTMLDivElement>(null);
    const [position, setPosition] = useState({ top: 0, left: 0 });
    const bodyRef = useRef<HTMLDivElement>(null);

    useEffect(() => {
        if (!isOpen || !triggerElement || !popoverRef.current) return;

        const updatePosition = () => {
            const triggerRect = triggerElement.getBoundingClientRect();
            const popoverRect = popoverRef.current!.getBoundingClientRect();
            const viewportWidth = window.innerWidth;
            const viewportHeight = window.innerHeight;

            let top = triggerRect.bottom + 8;
            let left = triggerRect.left;

            if (left + popoverRect.width > viewportWidth) {
                left = viewportWidth - popoverRect.width - 16;
            }

            if (top + popoverRect.height > viewportHeight) {
                top = triggerRect.top - popoverRect.height - 8;
            }

            setPosition({ top, left });
        };

        updatePosition();
        window.addEventListener('resize', updatePosition);
        window.addEventListener('scroll', updatePosition, true);

        return () => {
            window.removeEventListener('resize', updatePosition);
            window.removeEventListener('scroll', updatePosition, true);
        };
    }, [isOpen, triggerElement]);

    useEffect(() => {
        if (!isOpen) return;

        const handleClickOutside = (e: MouseEvent) => {
            if (popoverRef.current && !popoverRef.current.contains(e.target as Node) && !triggerElement?.contains(e.target as Node)) {
                onClose();
            }
        };

        const handleEscape = (e: KeyboardEvent) => {
            if (e.key === 'Escape') {
                onClose();
            }
        };

        document.addEventListener('mousedown', handleClickOutside);
        document.addEventListener('keydown', handleEscape);

        return () => {
            document.removeEventListener('mousedown', handleClickOutside);
            document.removeEventListener('keydown', handleEscape);
        };
    }, [isOpen, onClose, triggerElement]);

    useEffect(() => {
        if (!bodyRef.current || !hasMore || !onLoadMore) return;

        const handleScroll = () => {
            const { scrollTop, scrollHeight, clientHeight } = bodyRef.current!;
            if (scrollHeight - scrollTop - clientHeight < 50 && !loading) {
                onLoadMore();
            }
        };

        const bodyElement = bodyRef.current;
        bodyElement.addEventListener('scroll', handleScroll);

        return () => {
            bodyElement.removeEventListener('scroll', handleScroll);
        };
    }, [hasMore, loading, onLoadMore]);

    if (!isOpen) return null;

    const formatDate = (date?: Date) => {
        if (!date) return '-';
        const d = new Date(date);
        return d.toLocaleString('en-US', {
            month: 'short',
            day: 'numeric',
            hour: '2-digit',
            minute: '2-digit',
        });
    };

    return (
        <>
            <div className="fixed inset-0 z-40" />
            <div
                ref={popoverRef}
                className="fixed z-50 bg-white rounded-xl shadow-2xl border border-gray-200 overflow-hidden"
                style={{
                    top: `${position.top}px`,
                    left: `${position.left}px`,
                    width: '480px',
                    maxHeight: '600px',
                    display: 'flex',
                    flexDirection: 'column',
                }}
            >
                <div className="px-5 py-4 border-b border-gray-200 bg-linear-to-r from-indigo-50 to-purple-50 shrink-0">
                    <div className="flex items-center justify-between">
                        <div>
                            <h3 className="text-lg font-bold text-gray-900">Recipient Status Details</h3>
                            <p className="text-sm text-gray-600 mt-1">
                                Showing {recipients.length} of {total} recipients
                            </p>
                        </div>
                        <button
                            onClick={onClose}
                            className="text-gray-400 hover:text-gray-600 transition-colors p-1 rounded-lg hover:bg-white/50"
                        >
                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                            </svg>
                        </button>
                    </div>
                </div>

                <div ref={bodyRef} className="flex-1 overflow-y-auto" style={{ minHeight: '200px', maxHeight: '500px' }}>
                    {recipients.length === 0 ? (
                        <div className="flex items-center justify-center h-40 text-gray-500">
                            <div className="text-center">
                                <svg className="w-12 h-12 mx-auto mb-2 opacity-40" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                        strokeWidth={2}
                                        d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4"
                                    />
                                </svg>
                                <p className="text-sm">No recipients found</p>
                            </div>
                        </div>
                    ) : (
                        <div className="divide-y divide-gray-100">
                            {recipients.map((recipient, index) => (
                                <div
                                    key={recipient.id}
                                    className={classnames(
                                        'px-5 py-3 hover:bg-gray-50 transition-colors',
                                        index % 2 === 0 ? 'bg-white' : 'bg-gray-50/50'
                                    )}
                                >
                                    <div className="flex items-start justify-between gap-3">
                                        <div className="flex-1 min-w-0">
                                            <div className="flex items-center gap-2 mb-1">
                                                <p className="text-sm font-semibold text-gray-900 truncate">{recipient.name || '-'}</p>
                                            </div>
                                            <p className="text-xs text-gray-600 font-mono">{recipient.mobile}</p>
                                            {recipient.errorMessage && (
                                                <p className="text-xs text-red-600 mt-1 line-clamp-2" title={recipient.errorMessage}>
                                                    {recipient.errorMessage}
                                                </p>
                                            )}
                                        </div>
                                        <div className="shrink-0 text-right">
                                            <span
                                                className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-semibold"
                                                style={{
                                                    backgroundColor: `${statusColorMap[recipient.status] || '#6b7280'}15`,
                                                    color: statusColorMap[recipient.status] || '#6b7280',
                                                }}
                                            >
                                                {statusLabelMap[recipient.status] || recipient.status}
                                            </span>
                                            <p className="text-xs text-gray-500 mt-1.5">{formatDate(recipient.statusDate)}</p>
                                        </div>
                                    </div>
                                </div>
                            ))}
                            {loading && (
                                <div className="px-5 py-4 text-center">
                                    <div className="inline-flex items-center gap-2 text-sm text-gray-500">
                                        <svg className="animate-spin h-4 w-4" fill="none" viewBox="0 0 24 24">
                                            <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
                                            <path
                                                className="opacity-75"
                                                fill="currentColor"
                                                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                                            />
                                        </svg>
                                        Loading more...
                                    </div>
                                </div>
                            )}
                        </div>
                    )}
                </div>
            </div>
        </>
    );
};
