import React from 'react';

type StatCardProps = {
    label: string;
    value: number;
    color: string;
    percentage?: string;
    icon?: string;
    onClickDetails?: (e: React.MouseEvent<HTMLDivElement>) => void;
};

const StatCard: React.FC<StatCardProps> = ({ label, value, color, percentage, icon, onClickDetails }) => (
    <div
        className={`relative overflow-hidden rounded-xl bg-linear-to-br from-white to-gray-50 p-4 border border-gray-200 hover:shadow-md transition-shadow ${
            onClickDetails && value > 0 ? 'cursor-pointer' : ''
        }`}
        onClick={onClickDetails && value > 0 ? onClickDetails : undefined}
    >
        {icon && (
            <div className="absolute top-2 right-2 text-3xl opacity-40 pointer-events-none" style={{ color }}>
                {icon}
            </div>
        )}
        <div className="relative">
            <p className="text-xs font-medium text-gray-500 uppercase tracking-wide mb-1">{label}</p>
            <div className="flex items-baseline gap-2">
                <p className="text-2xl font-bold" style={{ color }}>
                    {value.toLocaleString()}
                </p>
                {percentage && <span className="text-sm font-medium text-gray-400">{percentage}%</span>}
            </div>
        </div>
    </div>
);

export default StatCard;
