import { Button } from 'ether-ui';
import { DownloadIcon } from 'ether-ui/icons';
import React, { useCallback, useRef, useState } from 'react';
import { Campaign, CampaignRecipient } from '../../../types/campaign';
import { RecipientStatusPopover } from '../../components/RecipientStatusPopover';
import { downloadCampaignAnalytics, getCampaignRecipients, useCampaignStore } from '../../services/campaign';
import { CampaignStatusBadge } from './CampaignStatusBadge';
import { ChannelBadge } from './ChannelBadge';
import StatCard from './StatCard';

interface CampaignCardProps {
    campaign: Campaign;
}

export const CampaignCard: React.FC<CampaignCardProps> = ({ campaign }) => {
    const { pauseCampaign, resumeCampaign } = useCampaignStore();
    const [isPopoverOpen, setIsPopoverOpen] = useState(false);
    const [popoverStatus, setPopoverStatus] = useState<string>('');
    const [recipients, setRecipients] = useState<CampaignRecipient[]>([]);
    const [recipientsTotal, setRecipientsTotal] = useState(0);
    const [recipientsLoading, setRecipientsLoading] = useState(false);
    const [recipientsOffset, setRecipientsOffset] = useState(0);
    const triggerRef = useRef<HTMLElement | null>(null);

    const handlePause = async () => {
        await pauseCampaign(campaign.id);
    };

    const handleResume = async () => {
        await resumeCampaign(campaign.id);
    };

    const loadRecipients = useCallback(
        async (status: string, offset: number = 0) => {
            setRecipientsLoading(true);
            try {
                const response = await getCampaignRecipients(campaign.id, { status, limit: 50, offset });
                if (offset === 0) {
                    setRecipients(response.recipients);
                } else {
                    setRecipients((prev) => [...prev, ...response.recipients]);
                }
                setRecipientsTotal(response.total);
                setRecipientsOffset(offset);
            } catch (error) {
                console.error('Failed to load recipients:', error);
            } finally {
                setRecipientsLoading(false);
            }
        },
        [campaign.id]
    );

    const handleShowDetails = useCallback(
        (status: string) => (e: React.MouseEvent<HTMLDivElement>) => {
            e.stopPropagation();
            triggerRef.current = e.currentTarget as any;
            setPopoverStatus(status);
            setIsPopoverOpen(true);
            setRecipients([]);
            setRecipientsOffset(0);
            loadRecipients(status, 0);
        },
        [loadRecipients]
    );

    const handleLoadMore = useCallback(() => {
        const nextOffset = recipientsOffset + 50;
        loadRecipients(popoverStatus, nextOffset);
    }, [loadRecipients, popoverStatus, recipientsOffset]);

    const handleClosePopover = useCallback(() => {
        setIsPopoverOpen(false);
        setRecipients([]);
        setRecipientsOffset(0);
    }, []);

    const handleDownloadAnalytics = useCallback(() => {
        downloadCampaignAnalytics(campaign.id);
    }, [campaign.id]);

    const pending = campaign.totalRecipients - campaign.sent - campaign.failed;
    const canPauseOrResume = pending > 0;

    const deliveryRate = campaign.sent > 0 ? ((campaign.delivered / campaign.sent) * 100).toFixed(1) : '0';
    const readRate = campaign.delivered > 0 ? ((campaign.read / campaign.delivered) * 100).toFixed(1) : '0';
    const responseRate = campaign.delivered > 0 ? ((campaign.responded / campaign.delivered) * 100).toFixed(1) : '0';
    const completionRate =
        campaign.totalRecipients > 0 ? (((campaign.sent + campaign.failed) / campaign.totalRecipients) * 100).toFixed(1) : '0';

    const hasMoreRecipients = recipients.length < recipientsTotal;

    return (
        <div className="bg-white border border-gray-200 rounded-2xl shadow-sm hover:shadow-lg transition-all duration-300 overflow-hidden">
            <div className="bg-linear-to-r from-indigo-50 via-purple-50 to-pink-50 px-6 py-4 border-b border-gray-200">
                <div className="flex items-start justify-between">
                    <div className="flex-1">
                        <div className="flex items-center gap-3 mb-2">
                            <h3 className="font-bold text-gray-900 text-xl">{campaign.name}</h3>
                            <ChannelBadge type={campaign.integrationType} />
                            <CampaignStatusBadge status={campaign.status} />
                        </div>
                        <div className="flex items-center gap-4 text-sm text-gray-600">
                            <span className="flex items-center gap-1">
                                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path
                                        strokeLinecap="round"
                                        strokeLinejoin="round"
                                        strokeWidth={2}
                                        d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z"
                                    />
                                </svg>
                                {new Date(campaign.createdAt).toLocaleDateString('en-US', {
                                    month: 'short',
                                    day: 'numeric',
                                    year: 'numeric',
                                })}
                            </span>
                            <span className="flex items-center gap-1">
                                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7h8m0 0v8m0-8l-8 8-4-4-6 6" />
                                </svg>
                                {completionRate}% Complete
                            </span>
                        </div>
                    </div>

                    <div className="flex gap-2">
                        <Button
                            variant="secondary"
                            layout="plain"
                            size="sm"
                            leftIcon={DownloadIcon}
                            onClick={handleDownloadAnalytics}
                            className="shadow-sm"
                        />
                        {canPauseOrResume && (
                            <>
                                {campaign.status === 'sending' && (
                                    <Button variant="default" size="sm" onClick={handlePause} className="shadow-sm">
                                        <svg className="w-4 h-4 mr-1" fill="currentColor" viewBox="0 0 24 24">
                                            <path d="M6 4h4v16H6V4zm8 0h4v16h-4V4z" />
                                        </svg>
                                        Pause
                                    </Button>
                                )}
                                {campaign.status === 'paused' && (
                                    <Button variant="primary" size="sm" onClick={handleResume} className="shadow-sm">
                                        <svg className="w-4 h-4 mr-1" fill="currentColor" viewBox="0 0 24 24">
                                            <path d="M8 5v14l11-7z" />
                                        </svg>
                                        Resume
                                    </Button>
                                )}
                            </>
                        )}
                    </div>
                </div>
            </div>

            <div className="p-6">
                <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-7 gap-3">
                    <StatCard
                        label="Total"
                        value={campaign.totalRecipients}
                        color="#6366f1"
                        icon="üìä"
                        onClickDetails={handleShowDetails('all')}
                    />
                    <StatCard label="Pending" value={pending} color="#f59e0b" icon="‚è≥" onClickDetails={handleShowDetails('pending')} />
                    <StatCard label="Sent" value={campaign.sent} color="#8b5cf6" icon="üì§" onClickDetails={handleShowDetails('sent')} />
                    <StatCard
                        label="Delivered"
                        value={campaign.delivered}
                        color="#10b981"
                        percentage={deliveryRate}
                        icon="‚úì"
                        onClickDetails={handleShowDetails('delivered')}
                    />
                    <StatCard
                        label="Read"
                        value={campaign.read}
                        color="#3b82f6"
                        percentage={readRate}
                        icon="üëÅ"
                        onClickDetails={handleShowDetails('read')}
                    />
                    <StatCard
                        label="Responded"
                        value={campaign.responded}
                        color="#ec4899"
                        percentage={responseRate}
                        icon="üí¨"
                        onClickDetails={handleShowDetails('responded')}
                    />
                    <StatCard
                        label="Failed"
                        value={campaign.failed}
                        color="#ef4444"
                        icon="‚ö†"
                        onClickDetails={handleShowDetails('failed')}
                    />
                </div>
            </div>

            <RecipientStatusPopover
                isOpen={isPopoverOpen}
                onClose={handleClosePopover}
                triggerElement={triggerRef.current}
                recipients={recipients}
                total={recipientsTotal}
                loading={recipientsLoading}
                onLoadMore={handleLoadMore}
                hasMore={hasMoreRecipients}
            />
        </div>
    );
};
